* {
  font-family: "Courier New", Courier, monospace, Arial, sans-serif;
  color:white;
  background-color: black;
}

body {
  padding: 60px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 175px;
}
.inputBox {
  background-color: blanchedalmond;
  color: black;
  font-family:  monospace;
}
